* { margin:0; padding: 0; }
li { list-style: none; }
a { text-decoration: none; }

.navbar {
    background-color: #0e0e0e;
    padding: 1rem;
}

.nav-links {
    display: flex;
    justify-content: center;
}
.nav-links li {
    margin: 0 1rem;
}

.nav-links li a {
    color: #fff;
    font-size: 1.2rem;
    transition: 0.3s;
}
.nav-links li a:hover {
    color: rgb(213, 17, 17);
}