* { margin:0; padding: 0; }
li { list-style: none; }
a { text-decoration: none; }

.contact-container {
    padding: 2rem;
    background-color: #dce8fd;
    text-align: justify;
    height: 100vh;
}
.contact-title {
    color: #3ba6f1;
    font-size: 2.3rem;
    margin-bottom: 1rem;
}
.contact-description {
    color: #333;
    font-size: 1.2rem;
    height: 100vh;
}